<template>
  <div class="mobile-container">
    <Header/>

    <div class="m-content">
        <div class="m-tips">
            {{ $t('lang.masternode_title') }}
        </div>
      <div>
        <el-table
          :data="tableData"
          style="">
          <el-table-column
            prop="address"
            :label="$t('lang.masternode_table1')"
            width="180">
          </el-table-column>
          <el-table-column
            prop="status"
            :label="$t('lang.masternode_table2')"
            width="86">
            <template slot-scope="scope">
              <span style="color: #000000;" v-if="scope.row.status === 'ENABLED'">{{ scope.row.status }}</span>
              <span style="color: #888888;" v-else>{{ scope.row.status }}</span>
            </template>
          </el-table-column>
          <el-table-column
            prop="version"
            :label="$t('lang.masternode_table3')"
            width="86">
          </el-table-column>
          <el-table-column
            prop="lasttime"
            :label="$t('lang.masternode_table4')"
            width="250">
            <template slot-scope="scope">
              {{ filterDate(scope.row.lasttime) }}
            </template>
          </el-table-column>
          <el-table-column
            prop="lasttime"
            :label="$t('lang.masternode_table5')"
            width="250">
            <template slot-scope="scope">
              {{ filterDate(scope.row.lastpaid) }}
            </template>
          </el-table-column>
        </el-table>
      </div>
    </div>
    <Footer/>
  </div>
</template>

<script>
import Header from '@/components/MHeader'
import Footer from '@/components/MFooter'
import pageSize from '@/components/MPage'
import dateFns from 'date-fns'
export default {
    components: {
        Header,
        Footer,
        pageSize
    },
    data() {
            return {
                tableData: []
            }
        },
        computed: {},
        mounted() {
            this.getNodeList()
        },
        methods: {
            getNodeList(){
                this.$http.get("api/node/list").then(res => {
                    if (res.data.code === 200){
                        this.tableData = res.data.data
                    }
                })
            },
            filterDate(value, type) {
                if (!value) return ''
                if(value > 0){
                    value = value * 1000
                }
                const filterType = type || 'YYYY-MM-DD HH:mm:ss Z'
                return dateFns.format(value, filterType)
            }
        }
}


</script>


<style scoped>
    .m-content{
        padding: 1rem;
        background: #E6E9EE;
    }
    .m-head{
        background: #ffffff;
    }
    .m-head .m-item{
        text-align: center;
        border-bottom: 1px solid #F0F0F0;
        padding: 1rem 0;
    }
    .m-item p{
        color: #888888;
        font-size: 12px;
    }
    .item-lebel{
        font-size: 17px;
        margin-bottom: .5rem;
    }
    .m-tips{
        padding: .7rem;
        text-align: left;
    }
</style>



